<template>
  <div class="app-videolist">
    <p class="title">{{temp_data.title}}</p>
    <template v-if="temp_data.list.length>0">
      <ul class="video-list">
        <li v-for="listItem in temp_data.list">
          <router-link :to="{
          name: 'App_product',
          query: {
          id: listItem.id
        }
        }">
            <img class="bg" :src="listItem.img_url">
            <img class="play" :src="play">
            <div class="title">{{listItem.title}}</div>
            <div class="description">{{listItem.description}}</div>
            <div class="ingredients">{{listItem.ingredients}}</div>
          </router-link>
        </li>
      </ul>
    </template>
    <template v-else>
      <p>没有匹配的菜谱。</p>
    </template>
    <div class="paginate" @click="clickpage" v-if="temp_data.pagemsg.sumpage>1">
      <template v-if="temp_data.pagemsg.sumpage>6">
        <template v-if="temp_data.pagemsg.nowpage<5">
          <template v-for="pages in 5">
            <a v-if="pages!=temp_data.pagemsg.nowpage">{{pages}}</a>
            <strong v-else>{{pages}}</strong>
          </template>
          <span>...</span>
          <a>{{temp_data.pagemsg.sumpage}}</a>
        </template>
        <template v-else-if="temp_data.pagemsg.nowpage>(temp_data.pagemsg.sumpage-4)">
          <a>1</a>
          <span>...</span>
          <template v-for="pages in 5">
            <a
              v-if="(temp_data.pagemsg.sumpage-5+pages)!=temp_data.pagemsg.nowpage">{{(temp_data.pagemsg.sumpage - 5 + pages)}}</a>
            <strong v-else>{{(temp_data.pagemsg.sumpage - 5 + pages)}}</strong>
          </template>
        </template>
        <template v-else>
          <a>1</a>
          <span>...</span>
          <a>{{(temp_data.pagemsg.nowpage - 2)}}</a>
          <a>{{(temp_data.pagemsg.nowpage - 1)}}</a>
          <strong>{{temp_data.pagemsg.nowpage}}</strong>
          <a>{{(temp_data.pagemsg.nowpage + 1)}}</a>
          <a>{{(temp_data.pagemsg.nowpage + 2)}}</a>
          <span>...</span>
          <a>{{temp_data.pagemsg.sumpage}}</a>
        </template>
      </template>
      <template v-else>
        <template v-for="pages in temp_data.pagemsg.sumpage">
          <a v-if="pages!=temp_data.pagemsg.nowpage">{{pages}}</a>
          <strong v-else>{{pages}}</strong>
        </template>
      </template>
    </div>
  </div>
</template>


<script>
  export default {
    name: 'app-videolist',
    props: ['videolist_data'],
    data() {
      return {
        temp_data: this.videolist_data,
        play: this.app_config.staticPath + '/images/play-big.png',
      }
    },
    watch: {
      videolist_data: function (val, oldVal) {
        this.temp_data = val;
      }
    },
    methods: {
      clickpage: function (event) {
        let self = this;
        if (event.target.nodeName === 'A') {
          let push_data = {
            name: self.$route.name,
            query: {
              page: parseInt(event.target.innerText)
            },
          };
          if (self.$route.query.name) {
            push_data.query.name = self.$route.query.name;
          }
          if (self.$route.query.type) {
            push_data.query.type = self.$route.query.type;
          }
          self.$router.push(push_data);
        }
      }
    },
    mounted() {
    }
  }
</script>

<style scoped>
  .app-videolist {
    width: 100%;
  }

  .app-videolist > .title {
    font-size: 20px;
    font-weight: bold;
    margin: 0 20px 0 0;
    padding-bottom: 8px;
    border-bottom: 1px solid #c6b363;
  }

  .app-videolist > .video-list {
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  .app-videolist > .video-list > li {
    margin-top: 25px;
    width: 33.33%;
    padding-right: 20px;
    box-sizing: border-box;
    float: left;
  }

  .app-videolist > .video-list > li > a {
    position: relative;
    width: 100%;
    display: block;
    text-decoration: none;
  }

  .app-videolist > .video-list > li > a:hover {
    opacity: 0.8;
    filter: alpha(opacity=80);
  }

  .app-videolist > .video-list > li > a > img.bg {
    display: block;
    width: 210px;
    height: 210px;
    background-image: url("../assets/images/loading-side.png");
    background-size: 100%;
  }

  .app-videolist > .video-list > li > a > img.play {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    padding: 35%;
    box-sizing: border-box;
  }

  .app-videolist > .video-list > li > a > .title {
    width: 100%;
    margin: 10px 0 6px 0;
    font-size: 16px;
    display: block;
    color: #262626;
    font-weight: bold;
    line-height: 1.3;
  }

  .app-videolist > .video-list > li > a > .description {
    width: 100%;
    color: #5e5e5e;
    font-size: 13px;
    margin: 6px 0;
  }

  .app-videolist > .video-list > li > a > .ingredients {
    width: 100%;
    color: #8E8E8E;
    font-size: 11px;
    margin: 6px 0 10px 0;
  }

  .app-videolist > .paginate {
    margin-top: 30px;
    width: 100%;
    text-align: center;
  }

  .app-videolist > .paginate > strong,
  .app-videolist > .paginate > a,
  .app-videolist > .paginate > span {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border: 1px solid #eee;
    border-bottom: 2px solid #eee;
    font-size: 16px;
    color: #262626;
    text-decoration: none;
    margin: auto 5px;
    cursor: default;
  }

  .app-videolist > .paginate > a {
    cursor: pointer;
  }

  .app-videolist > .paginate > strong {
    background-color: #c6b363;
    color: #fff;
  }

  .app-videolist > .paginate > span {
    border: 1px solid transparent;
    border-bottom: 2px solid transparent;
  }
</style>



