<template>
  <div class="app-specialinfo">
    <app-breadcrumb :breadcrumb_data="breadcrumb_data"></app-breadcrumb>
    <div class="middle-content">
      <div class="left">
        <img :src="imgsrc" alt="首图">
        <p class="title">{{title}}</p>
        <p class="description">{{description}}</p>
        <template v-if="videolist_data.list.length>0">
          <ul>
            <li v-for="listItem in videolist_data.list">
              <router-link :to="{
                name: 'App_product',
                query: {
                  id: listItem.id
                }
              }">
                <img class="bg" :src="listItem.img_url">
                <img class="play" :src="play">
                <div class="title">{{listItem.title}}</div>
                <div class="description">{{listItem.description}}</div>
                <div class="ingredients">{{listItem.ingredients}}</div>
              </router-link>
            </li>
          </ul>
        </template>
        <template v-else>
          <p>没有匹配的菜谱。</p>
        </template>
        <div class="paginate" @click="clickpage" v-if="videolist_data.pagemsg.sumpage>1">
          <template v-if="videolist_data.pagemsg.sumpage>6">
            <template v-if="videolist_data.pagemsg.nowpage<5">
              <template v-for="pages in 5">
                <a v-if="pages!=videolist_data.pagemsg.nowpage">{{pages}}</a>
                <strong v-else>{{pages}}</strong>
              </template>
              <span>...</span>
              <a>{{videolist_data.pagemsg.sumpage}}</a>
            </template>
            <template v-else-if="videolist_data.pagemsg.nowpage>(videolist_data.pagemsg.sumpage-4)">
              <a>1</a>
              <span>...</span>
              <template v-for="pages in 5">
                <a
                  v-if="(videolist_data.pagemsg.sumpage-5+pages)!=videolist_data.pagemsg.nowpage">{{(videolist_data.pagemsg.sumpage - 5 + pages)}}</a>
                <strong v-else>{{(videolist_data.pagemsg.sumpage - 5 + pages)}}</strong>
              </template>
            </template>
            <template v-else>
              <a>1</a>
              <span>...</span>
              <a>{{(videolist_data.pagemsg.nowpage - 2)}}</a>
              <a>{{(videolist_data.pagemsg.nowpage - 1)}}</a>
              <strong>{{videolist_data.pagemsg.nowpage}}</strong>
              <a>{{(videolist_data.pagemsg.nowpage + 1)}}</a>
              <a>{{(videolist_data.pagemsg.nowpage + 2)}}</a>
              <span>...</span>
              <a>{{videolist_data.pagemsg.sumpage}}</a>
            </template>
          </template>
          <template v-else>
            <template v-for="pages in videolist_data.pagemsg.sumpage">
              <a v-if="pages!=videolist_data.pagemsg.nowpage">{{pages}}</a>
              <strong v-else>{{pages}}</strong>
            </template>
          </template>
        </div>
      </div>
      <div class="right">
        <app-special :special_data="special_data"></app-special>
      </div>
    </div>
  </div>
</template>

<script>
  import app_breadcrumb from '@/components/App-breadcrumb.vue'
  import app_special from '@/components/App-special.vue'

  let special_defautData = [],
    videolist_defautData = {
      title: '',
      pagemsg: {
        nowpage: 0,
        sumpage: 0
      },
      list: []
    };

  export default {
    name: 'app-specialinfo',
    data() {
      return {
        play: this.app_config.staticPath + '/images/play-big.png',
        breadcrumb_data: ['首页', '特辑列表'],
        special_data: special_defautData,
        videolist_data: videolist_defautData,
        title: '',
        imgsrc: '',
        description: '',
      }
    },
    components: {
      'app-breadcrumb': app_breadcrumb,
      'app-special': app_special,
    },
    methods: {
      clickpage: function (event) {
        let self = this;
        if (event.target.nodeName === 'A') {
          let push_data = {
            name: self.$route.name,
            query: {
              page: parseInt(event.target.innerText)
            },
          };
          if (self.$route.query.id) {
            push_data.query.id = self.$route.query.id;
          }
          self.$router.push(push_data);
        }
      },
      getinfo: function () {
        let self = this;

        self.$http.get(self.app_config.basePath + '/specialinfoone', {
          params: {
            searchstr: self.$route.query.id,
          }
        }).then(function (response) {
          self.breadcrumb_data = ['首页', '特辑列表'];
          self.breadcrumb_data.push(response.data.name);
          self.title = response.data.name;
          self.imgsrc = response.data.showimg;
          self.description = response.data.content;
        }).catch(function (err) {
          console.log(err);
        });
      },
      getlist: function () {
        let self = this;
        self.$http.get(self.app_config.basePath + '/videolistinfo', {
          params: {
            msgjihe: self.$route.query.id,
            page: this.$route.query.page ? this.$route.query.page : 1,
            pagesize: 8
          }
        }).then(function (response) {
          let temp = {
              title: '',
              pagemsg: {
                nowpage: parseInt(response.data.pagemsg.nowpage),
                sumpage: parseInt(response.data.pagemsg.sumpage)
              },
              list: [],
            },
            titlelength = 20,
            descriptionlength = 30,
            ingredientslength = 15;
          response.data.list.forEach(function (item, index) {
            let obj = {
              id: item.id,
              img_url: item.img_url,
              title: item.title,
              description: item.description,
              ingredients: item.ingredients
            };
            if (obj.title.length >= titlelength) {
              obj.title = item.title.slice(0, titlelength) + '...';
            }
            if (obj.description.length >= descriptionlength) {
              obj.description = item.description.slice(0, descriptionlength) + '...';
            }
            if (obj.ingredients.length >= ingredientslength) {
              obj.ingredients = item.ingredients.slice(0, ingredientslength) + '...';
            }
            temp.list.push(obj);
          });
          self.videolist_data = temp;
        }).catch(function (err) {
          console.log(err);
        });
      }
    },
    watch: {
      '$route': function (route) {
        this.getinfo();
        this.getlist();
      }
    },
    mounted() {
      let self = this;
      self.getinfo();
      self.getlist();
      self.$http.get(self.app_config.basePath + '/specialinfo', {
        params: {}
      }).then(function (response) {
        let temp = [],
          titlelength = 25,
          descriptionlength = 40;
        response.data.forEach(function (item, index) {
          let obj = {
            id: item.id,
            title: item.title,
            description: item.description,
            img_url: item.img_url,
          };
          if (obj.title.length >= titlelength) {
            obj.title = item.title.slice(0, titlelength) + '...';
          }
          if (obj.description.length >= descriptionlength) {
            obj.description = item.description.slice(0, descriptionlength) + '...';
          }
          temp.push(obj);
        });
        self.special_data = temp;
      }).catch(function (err) {
        console.log(err);
      });
    }
  }
</script>

<style scoped>
  .app-specialinfo {
    width: 100%;
  }

  .app-specialinfo > .middle-content {
    width: 1000px;
    margin: auto;
    padding: 40px 0;
    overflow: hidden;
  }

  .app-specialinfo > .middle-content > .left {
    float: left;
    width: 689px;
    font-size: 16px;
    box-sizing: border-box;
  }

  .app-specialinfo > .middle-content > .right {
    float: right;
    width: 311px;
  }

  .app-specialinfo > .middle-content > .left > img {
    display: block;
    width: 100%;
    margin-right: 20px;
    height: 376px;
    background-image: url("../assets/images/loading-side.png");
  }

  .app-specialinfo > .middle-content > .left > .title {
    margin: 40px 20px 12px 0;
    padding: 0 0 16px;
    font-size: 26px;
    font-weight: bold;
  }

  .app-specialinfo > .middle-content > .left > .description {
    padding-bottom: 24px;
    margin: 0 20px 12px 0;
    font-size: 14px;
    line-height: 1.5;
    border-bottom: 1px solid #c6b363;
  }

  .app-specialinfo > .middle-content > .left > ul {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
  }

  .app-specialinfo > .middle-content > .left > ul > li {
    width: 50%;
    float: left;
    margin: 20px 0;
    padding: 0 20px 0 0;
    box-sizing: border-box;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a {
    position: relative;
    width: 100%;
    display: block;
    text-decoration: none;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a:hover {
    opacity: 0.8;
    filter: alpha(opacity=80);
  }

  .app-specialinfo > .middle-content > .left > ul > li > a > img.bg {
    display: block;
    width: 100%;
    height: 324.5px;
    background-image: url("../assets/images/loading-side.png");
    background-size: 100%;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a > img.play {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    padding: 40%;
    box-sizing: border-box;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a > .title {
    width: 100%;
    margin: 10px 0 6px 0;
    font-size: 16px;
    display: block;
    color: #262626;
    font-weight: bold;
    line-height: 1.3;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a > .description {
    width: 100%;
    color: #5e5e5e;
    font-size: 13px;
    margin: 6px 0;
  }

  .app-specialinfo > .middle-content > .left > ul > li > a > .ingredients {
    width: 100%;
    color: #8E8E8E;
    font-size: 11px;
    margin: 6px 0 10px 0;
  }

  .app-specialinfo > .middle-content > .left > .paginate {
    margin-top: 30px;
    width: 100%;
    text-align: center;
  }

  .app-specialinfo > .middle-content > .left > .paginate > strong,
  .app-specialinfo > .middle-content > .left > .paginate > a,
  .app-specialinfo > .middle-content > .left > .paginate > span {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border: 1px solid #eee;
    border-bottom: 2px solid #eee;
    font-size: 16px;
    color: #262626;
    text-decoration: none;
    margin: auto 5px;
    cursor: default;
  }

  .app-specialinfo > .middle-content > .left > .paginate > a {
    cursor: pointer;
  }

  .app-specialinfo > .middle-content > .left > .paginate > strong {
    background-color: #c6b363;
    color: #fff;
  }

  .app-specialinfo > .middle-content > .left > .paginate > span {
    border: 1px solid transparent;
    border-bottom: 2px solid transparent;
  }
</style>



