<template>
  <div class="app-breadcrumb">
    <ul>
      <li v-for="(listItem,mindex) in breadcrumb_data">
        <template v-if="mindex==0">
          <router-link :to="{name:'App_home'}">{{listItem}}</router-link>
        </template>
        <template v-else-if="mindex==1&&listItem=='特辑列表'">
          <span :data-id="mindex" @click="gotospeciallist">{{listItem}}</span>
        </template>
        <template v-else-if="mindex<breadcrumb_data.length-1">
          <span :data-id="mindex" @click="gotoclassifyinfo">{{listItem}}</span>
        </template>
        <template v-else>
          <span>{{listItem}}</span>
        </template>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'app-breadcrumb',
    props: ['breadcrumb_data'],
    data() {
      return {}
    },
    methods: {
      gotospeciallist: function (event) {
        window.localStorage.setItem('temp_breadcrumb', JSON.stringify([]));
        this.$router.push({
          name: 'App_speciallist',
        });
      },
      gotoclassifyinfo: function (event) {
        let self = this,
          n = event.target.getAttribute('data-id'),
          temp_breadcrumb = new Array('首页');
        for (let i = 1; i < n; i++) {
          temp_breadcrumb.push(self.breadcrumb_data[i]);
        }
        window.localStorage.setItem('temp_breadcrumb', JSON.stringify(temp_breadcrumb));
        this.$router.push({
          name: 'App_classifyinfo',
          query: {
            name: event.target.innerHTML,
            type: 'classify' + n
          }
        });
      }
    },
    watch: {
      breadcrumb_data: function (val, oldVal) {
//        console.log('special_data发生改变！');
//        console.log('新值:');
//        console.log(val);
//        console.log('旧值:');
//        console.log(oldVal);
      }
    }
  }
</script>

<style scoped>
  .app-breadcrumb {
    width: 100%;
    box-shadow: 0 3px 3px -3px #cfcfcf;
  }

  .app-breadcrumb > ul {
    display: block;
    width: 1000px;
    margin: auto;
    padding: 0;
    line-height: 45px;
    list-style: none;
    overflow: hidden;
  }

  .app-breadcrumb > ul > li {
    float: left;
  }

  .app-breadcrumb > ul > li:after {
    content: '>';
    font-size: 14px;
    font-weight: bold;
    color: #cfcfcf;
    margin: 0 5px;
  }

  .app-breadcrumb > ul > li:last-child:after {
    content: '';
    margin: 0;
  }

  .app-breadcrumb > ul > li > * {
    text-decoration: none;
    color: #5e5e5e;
    cursor: pointer;
  }

  .app-breadcrumb > ul > li:last-child > span {
    font-weight: bold;
    cursor: default;
  }
</style>



