<template>
  <div class="app-head">
    <div class="head-content">
      <router-link class="logo" :to="{
          name: 'App_home',
        }">
        <img src="../assets/images/logo.png" alt="logo" :title="head_data.logo">
      </router-link>
      <div class="subhead" v-html="head_data.subhead"></div>
      <div class="search">
        <img src="../assets/images/search.png" alt="" @click="search_func">
        <input v-model="search_data" @keyup.enter="search_func" oninput='if(value.length>15)value=value.slice(0,15)'
               type="text" placeholder="使用料理名·食材搜索食谱">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-head',
    data() {
      return {
        head_data: {
          logo: '60seconds',
          subhead: '通过烹饪视频（食谱录像）\<br>以易于理解的方式介绍食谱！',
        },
        search_data: '',
      }
    },
    methods: {
      search_func: function (event) {
        let self = this;
        self.search_data = self.search_data.trim();
        let pattern = new RegExp("[~'!@#$%^&*()-+_=:]");
        if (self.search_data != '') {
          if (pattern.test(self.search_data)) {
            alert("非法字符！");
            event.target.focus();
            return false;
          } else {
            self.$router.push({
              name: 'App_search', query: {
                search_data: self.search_data
              }
            });
          }
        } else {
          alert('输入不能为空');
        }
      }
    }
  }
</script>

<style scoped>
  .app-head {
    background-color: #FFF;
    box-shadow: 0 0 4px #cfcfcf;
    width: 100%;
    min-width: 1000px;
  }

  .app-head > .head-content {
    width: 1000px;
    height: 50px;
    margin: auto;
    padding: 16px 0;
  }

  .app-head > .head-content > .logo {
    float: left;
    display: block;
    color: #C6B363;
    text-decoration: none;
    cursor: pointer;
  }

  .app-head > .head-content > .logo > img {
    display: block;
    margin: 5px auto;
    width: 250px;
    height: 40px;
  }

  .app-head > .head-content > .subhead {
    float: left;
    margin-left: 40px;
    color: #5e5e5e;
    line-height: 25px;
  }

  .app-head > .head-content > .search {
    position: relative;
    float: right;
    background-color: #eee;
    height: 40px;
    width: 320px;
    margin: 5px auto;
    border-radius: 2px;
    color: #5e5e5e;
  }

  .app-head > .head-content > .search > img {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 40px;
    height: 40px;
    padding: 12.5px;
    z-index: 1;
    cursor: pointer;
  }

  .app-head > .head-content > .search > input {
    display: block;
    box-sizing: border-box;
    border: 0;
    border-radius: 2px;
    position: absolute;
    top: 0;
    left: 0;
    height: 40px;
    width: 320px;
    background-color: transparent;
    padding: 0 16px 0 40px;
    outline: 0;
  }
</style>



