<template>
  <div class="app-speciallist">
    <div class="middle-content">
      <p class="title">特辑列表</p>
      <ul>
        <li v-for="listItem in special_data">
          <router-link :to="{
          name:'App_specialinfo',
          query: {
            id:listItem.id
            }
          }">
            <img :src="listItem.img_url">
            <div class="title">{{listItem.title}}</div>
            <div class="description">{{listItem.description}}</div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-speciallist',
    data() {
      return {
        special_data: []
      }
    },
    methods: {},
    watch: {
      '$route': function (route) {
//        this.search_data = this.$route.query.search_data;
//        this.getvideolistinfo();
      }
    },
    mounted() {
      let self = this;
      self.$http.get(self.app_config.basePath + '/specialinfo', {
        params: {
          pagesize: 15
        }
      }).then(function (response) {
        let temp = [],
          titlelength = 25,
          descriptionlength = 40;
        response.data.forEach(function (item, index) {
          let obj = {
            id: item.id,
            title: item.title,
            description: item.description,
            img_url: item.img_url,
          };
          if (obj.title.length >= titlelength) {
            obj.title = item.title.slice(0, titlelength) + '...';
          }
          if (obj.description.length >= descriptionlength) {
            obj.description = item.description.slice(0, descriptionlength) + '...';
          }
          temp.push(obj);
        });
        self.special_data = temp;
      }).catch(function (err) {
        console.log(err);
      });
    }
  }
</script>

<style scoped>
  .app-speciallist {
    width: 100%;
  }

  .app-speciallist > .middle-content {
    width: 1000px;
    margin: auto;
    padding: 40px 0;
    overflow: hidden;
  }

  .app-speciallist > .middle-content > .title {
    margin: 0;
    font-size: 26px;
    padding-bottom: 10px;
    font-weight: bold;
    border-bottom: 1px solid #c6b363;
  }

  .app-speciallist > .middle-content > ul {
    width: 100%;
    list-style: none;
    margin: 0 -20px 0 0;
    padding: 0;
    overflow: hidden;
  }

  .app-speciallist > .middle-content > ul > li {
    width: 320px;
    float: left;
    margin: 20px 20px 0 0;
  }

  .app-speciallist > .middle-content > ul > li:nth-child(3n) {
    margin-right: 0;
  }

  .app-speciallist > .middle-content > ul > li > a {
    text-decoration: none;
    display: block;
    width: 100%;
  }

  .app-speciallist > .middle-content > ul > li > a:hover {
    opacity: 0.8;
    filter: alpha(opacity=80);
  }

  .app-speciallist > .middle-content > ul > li > a > img {
    width: 100%;
    height: 180px;
  }

  .app-speciallist > .middle-content > ul > li > a > .title {
    margin: 10px 0;
    font-size: 16px;
    line-height: 1.3;
    font-weight: bold;
    color: #262626;
  }

  .app-speciallist > .middle-content > ul > li > a > .description {
    color: #5e5e5e;
    font-size: 13px;
    margin: 8px 0;
    line-height: 1.3;
  }
</style>



