<template>
  <div class="app-popup" @click="colse()">
    <div class="popup-content">
      <div class="app-download">
        <div class="app-link-banner-title">我们来看看配方视频的应用程序。</div>
        <div class="app-link-banner-description">如果是kurashiru的应用程序，除了喜欢的保存等有用的功能之外，限制应用程序的配方也逐个发送！</div>
        <div class="app-link-banner-btn">
          <a href=""><img src="../assets/images/banner-apple.png" alt="APP苹果下载"></a>
          <a href=""><img src="../assets/images/banner-android.png" alt="APP安卓下载"></a>
        </div>
        <img src="../assets/images/banner-phone.png" class="banner-phone" alt="APP示例">
      </div>
      <img @click="colse()" class="colse" src="../assets/images/colse.png" alt="关闭弹出层">
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-popup',
    props: ['show_popup'],
    data() {
      return {}
    },
    methods: {
      colse: function () {
        this.$emit('c_show_popup', false);
      }
    },

  }
</script>

<style scoped>
  .app-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    z-index: 99999;
  }

  .app-popup > .popup-content {
    position: absolute;
    top: 50%;
    left: 50%;
    box-sizing: border-box;
    width: 840px;
    height: 240px;
    padding: 40px;
    margin: -120px 0 0 -420px;
    background-color: #f1f1f2;
    border-radius: 2px;
  }

  .app-popup > .popup-content > .colse {
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 15px;
  }

  .app-popup > .popup-content > .app-download > .app-link-banner-title {
    font-size: 28px;
    color: #262626;
    letter-spacing: 1px;
    font-weight: bold;
  }

  .app-popup > .popup-content > .app-download > .app-link-banner-description {
    width: 450px;
    margin-top: 20px;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 2px;
    color: #5e5e5e;
  }

  .app-popup > .popup-content > .app-download > .banner-phone {
    position: absolute;
    right: 40px;
    bottom: 0;
    display: block;
    width: 300px;
  }

  .app-popup > .popup-content > .app-download > .app-link-banner-btn {
    position: absolute;
    left: 40px;
    bottom: 40px;
    height: 40px;
  }

  .app-popup > .popup-content > .app-download > .app-link-banner-btn > a {

  }

  .app-popup > .popup-content > .app-download > .app-link-banner-btn > a > img {
    display: inline-block;
    width: 130px;
  }

  .app-popup > .popup-content > .app-download > .app-link-banner-btn > a:last-child {
    margin-left: 30px;
  }
</style>



