<template>
  <div class="app-special">
    <p class="title">特辑</p>
    <ul class="special-list">
      <li v-for="listItem in special_data">
        <router-link :to="{
          name:'App_specialinfo',
          query: {
            id:listItem.id
            }
          }">
          <img :src="listItem.img_url">
          <div class="title">{{listItem.title}}</div>
          <div class="description">{{listItem.description}}</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'app-special',
    props: ['special_data'],
    data() {
      return {}
    },
    methods: {},
    watch: {
      special_data: function (val, oldVal) {
//        console.log('special_data发生改变！');
//        console.log('新值:');
//        console.log(val);
//        console.log('旧值:');
//        console.log(oldVal);
      }
    },
  }
</script>

<style scoped>
  .app-special {
    width: 100%;
    box-sizing: border-box;
    padding-left: 20px;
    border-left: 1px solid #cfcfcf;
  }

  .app-special > .title {
    font-size: 20px;
    font-weight: bold;
    margin: auto;
    padding-bottom: 8px;
    border-bottom: 1px solid #c6b363;
  }

  .app-special > .special-list {
    width: 100%;
    list-style: none;
    margin: auto;
    padding: 0;
    overflow: hidden;
  }

  .app-special > .special-list > li {
    width: 100%;
    margin-top: 25px;
    float: left;
  }

  .app-special > .special-list > li > a {
    width: 100%;
    display: block;
    text-decoration: none;

  }

  .app-special > .special-list > li > a:hover {
    opacity: 0.8;
    filter: alpha(opacity=80);
  }

  .app-special > .special-list > li > a > img {
    display: block;
    width: 100%;
    height: 170px;
    background-image: url("../assets/images/loading-long.png");
    background-size: 100%;
  }

  .app-special > .special-list > li > a > .title {
    width: 100%;
    margin: 10px 0 6px 0;
    font-size: 16px;
    display: block;
    color: #262626;
    font-weight: bold;
    line-height: 1.5;
  }

  .app-special > .special-list > li > a > .description {
    width: 100%;
    color: #5e5e5e;
    font-size: 12px;
    margin: 6px 0 10px 0;
    line-height: 1.5;
  }
</style>



